import React from "react";
import { IEntry, IEntryState } from "data/entry";

interface IProps extends IEntryState {
  searchTerm: string;
  setSearchResults: any;
  selected: IEntry;
  setSelected: any;
  setMode: any;
}

export const List: React.FunctionComponent<IProps> = (props) => {
  const { entries, selected, setSelected, setMode } = props;

  const selectActivity = (activity: IEntry) => {
    setSelected(activity);
    setMode(0);
  };

  return (
    <div className="activities">
      {/* ACTIVITY */}
      {/* <ReactSortable className="sortable" list={entries} setList={setSearchResults}> */}
      {entries.map((activity) => (
        <div key={activity.id}>
          {/* <div>{CategoryTypes[activity.categoryId]}</div> */}
          <div
            className={
              activity.id === selected.id ? "activity selected" : "activity"
            }
            onClick={() => selectActivity(activity)}
          >
            <div className="activity-icon">
              <a>SE</a>
            </div>
            <div className="activity-data">
              <div className="activity-title">{activity.title}</div>
              <div className="activity-read-time">5 minutes read</div>
            </div>
          </div>
        </div>
      ))}
      {/* </ReactSortable> */}
    </div>
  );
};

export default List;
