import React from "react";
import Menu from "components/UI/Menu";

import { usePersistMenu } from "hooks/usePersistMenu";

export const MainLayout: React.FunctionComponent = (props) => {
  const [menu, toggleMenu] = usePersistMenu();
  const menuMode = menu === "true" ? true : false;

  return (
    <div className="wrapper">
      <Menu menuMode={menuMode} toggleMenu={toggleMenu} />
      {props.children}
    </div>
  );
};

export default MainLayout;
