import { useEffect, useState } from "react";

export const usePersistMenu = () => {
  const [menu, setMenu] = useState("true");

  const toggleMenu = () => {
    if (menu === "true") {
      localStorage.setItem("menuState", "false");
      setMenu("false");
    } else {
      localStorage.setItem("menuState", "true");
      setMenu("true");
    }
  };

  useEffect(() => {
    const localMenu = localStorage.getItem("menuState");
    if (localMenu) {
      setMenu(localMenu);
    } else {
      setMenu("false");
      localStorage.setItem("menuState", "false");
    }
  }, []);

  return [menu, toggleMenu];
};
