import React, { useState, FormEvent } from "react";
import { IEntry } from "data/entry";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { addEntry } from "store/entries/actions";
import { useFormFields } from "hooks/useFormFields"

interface IProps extends IEntry {
  setMode: any;
  setSelected: any;
}

export const NewSideView: React.FunctionComponent<IProps> = (props) => {
  const { setMode, setSelected } = props;

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange] = useFormFields({
    id: Math.floor(Math.random() * 10000),
    title: "",
    body: "",
    categoryId: 1,
    date: new Date().toJSON().slice(0, 10).replace(/-/g, '/'),
    userId: 13
  });

  const tabs = ["Activity", "Goal"];
  const [tabMode, setTabMode] = useState(0);

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(addEntry(fields));
    setMode(0);
    setSelected(fields)
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="activity-fn">
        <div className="item"></div>
      </div>
      <div className="activity">
        <div className="activity-menus">
          {/* Show all tabs and set selected class  */}
          {tabs.map((tab, index) => (
            <div key={index} className={index === tabMode ? "item selected" : "item"}>
              <a onClick={() => setTabMode(index)}>{tab}</a>
            </div>
          ))}
          <div className="item complete">
            <input type="submit" value="save" />
          </div>
        </div>
        <div className="activity-content">
          <div className="activity-content-title">
            <input id="title" placeholder="Add title..." value={fields.title} onChange={handleFieldChange} />
          </div>
          <div className="activity-content-body">
            <textarea id="body" placeholder="Description..." value={fields.body} onChange={handleFieldChange}></textarea>
          </div>
        </div>
      </div>
    </form>
  );
};

export default NewSideView;
