import React from "react";
import { NavLink } from "react-router-dom";
import { routes } from "data/routes";
import { menuIcons } from "data/featherIcons";
import johnDoe from "assets/img/John-Doe.jpg";

interface IRouteParams {
  toggleMenu: any;
  menuMode: boolean;
}

export const Menu: React.FunctionComponent<IRouteParams> = (props) => {
  const [homeIcon, usersIcon, goalIcon, ActivityIcon] = menuIcons;

  if (props.menuMode) {
    return (
      <nav role="navigation" className="navbar">
        {/* <div className="settings">
          <button onClick={props.toggleTheme}>Toggle theme</button>
          <button onClick={props.toggleMenu}>Aan</button>
        </div> */}

        <div className="logo">
          <NavLink exact to={routes.Home} className="logo">
            Super
          </NavLink>
        </div>
        <div className="main">
          <NavLink exact to={routes.Home} className="route">
            <i className="icon">{homeIcon}</i>
          </NavLink>
          <NavLink to={routes.Entries} className="route">
            <i className="icon">{usersIcon}</i>
          </NavLink>
          <NavLink exact to={routes.Profile} className="route">
            <i className="icon">{goalIcon}</i>
          </NavLink>
          <NavLink exact to={routes.Settings} className="route">
            <i className="icon">{ActivityIcon}</i>
          </NavLink>
          <NavLink exact to={routes.Admin} className="route">
            <i className="icon">&#xe801;</i>
          </NavLink>
        </div>
        <div className="profile">
          <img src={johnDoe} alt="John Doe" />
        </div>
      </nav>
    );
  } else {
    return <button onClick={props.toggleMenu}>Uit</button>;
  }
};

export default Menu;
