import React, { useState } from "react";
import { IEntry } from "data/entry";
import { sideviewIcons } from "data/featherIcons";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { removeEntry } from "store/entries/actions";

interface Iprops extends IEntry {
  setSelected: any;
  first: IEntry;
}

export const SideView: React.FunctionComponent<Iprops> = (props) => {
  const dispatch = useTypedDispatch();

  const { id, title, body, date, categoryId, userId, setSelected, first } = props;
  const [editIcon, deleteIcon] = sideviewIcons;

  const tabs = ["Activity", "My Notes", "Goal"];
  const [tabMode, setTabMode] = useState(0);

  const deleteItem = () => {
    dispatch(removeEntry(id));
    setSelected(first)
  }

  return (
    <div>
      {/* {tabMode} */}
      <div className="activity-fn">
        <div className="item">Last edit at {date}</div>
        <div className="item edit">{editIcon}</div>
        <div className="item delete" onClick={deleteItem}>{deleteIcon}</div>
      </div>
      <div className="activity">
        <div className="activity-menus">
          {/* Show all tabs and set selected class  */}
          {tabs.map((tab, index) => (
            <div key={index} className={index === tabMode ? "item selected" : "item"}>
              <a onClick={() => setTabMode(index)}>{tab}</a>
            </div>
          ))}
          <div className="item complete">
            <input type="submit" value="click to complete" />
          </div>
        </div>
        <div className="activity-content">
          <div className="activity-content-title">{title}</div>
          <div className="activity-content-body">{body}</div>
        </div>
      </div>
    </div>
  );
};

export default SideView;
